@import "../variables/colors";
@import "../variables/fonts";
@import "../variables/sizing";

 body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    background-color: $color-black;
    font-weight: $text-weight;
    color: $color-white;
    font-family: $main-font;
    cursor: url('.//../../images/cursor.png'), auto;
}

