.page-wrapper {
    width: 100%;
    overflow: hidden;
}

a {
    color: $color-white;
}

a:hover {
    cursor: url('.//../../images/cursor2.png'), auto;
}

button:hover {
    cursor: url('.//../../images/cursor2.png'), auto;
}

.button {
    background-color: $color-white;
    width: 184px;
    padding: 20px 0 20px 0;
    font-size: $text5-size;
    font-weight: $bold-weight;
}

.button2 {
    position: absolute;
    right: 0;
}

.container {
    max-width: 1200px;
    width: 90%;
    margin: 0 auto;
        @media (max-width: $phone-size) {
            width: 85%;
        }
}

.title-main {
    font-size: $title-size;
    font-weight: $bold-weight;
    @media (max-width: $phone-size) {
        font-size: $title2-size;
    }
}

.title2-main {
    font-size: $title2-size;
    @media (max-width: $phone-size) {
        font-size: $text2-size;
    }
}

.text {
    font-size: $text5-size;
    font-weight: $bold-weight;
}