@import '../../assets/styles/variables/fonts.scss';
@import '../../assets/styles/variables/colors.scss';
@import '../../assets/styles/variables/sizing.scss';

.games {
    padding: 170px 0;

    &__title {
        font-weight: $bold-weight;
        margin-bottom: 41px;
    }

    &__text {
        font-size: $text3-size;
        max-width: 1000px;
        margin-bottom: 60px;
    }

    &__block {
        margin-bottom: 130px;
    }

    &__game1 {
        width: 100%;
        margin-bottom: 60px;
    }

    .games-block-img {
        @media (max-width: $phone-size) {
            display: none;
        }
    }

    .games-block-img-mob {
        display: none;
        @media (max-width: $phone-size) {
            display: block;
        }
    }

    &__game-mob {
        width: 90%;
    }
}