@import '../../assets/styles/variables/fonts.scss';
@import '../../assets/styles/variables/colors.scss';
@import '../../assets/styles/variables/sizing.scss';

.main {
    position: relative;

    // &__container {
    //     @media (max-width: $phone-size) {
    //         width: 85%;
    //     }
    // }

    &__main {
        position: relative;
        width: 100vw;
        height: 100vh;
        @media (max-width: $phone-size) {
            padding: 220px 0 0 0;
            width: 90%;
            margin: 0 auto;
            height: 100%;
        }
    }
    &__bg {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        max-height: 100%;
        z-index: -1;
        object-fit: cover; 
        margin: 0;
        padding: 0;
        @media (max-width: $phone-size) {
            display: none;
        }
    }

    &__title-block {
        width: 580px;
        height: 146px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        @media (max-width: $phone-size) {
            display: none;
        }
    }

    &__title-mobile-block {
        display: none;
        @media (max-width: $phone-size) {
            display: block;
            margin-bottom: 150px;
        }
    }

    &__title {
        position: absolute;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }

    &__title1 {
        left: 0;
        top: 0;
        font-size: 55px;
    }
    &__title2 {
        display: flex;
        align-items: center;
        font-size: 55px;
        gap: 20px;
        right: 0;
        bottom: 0;
    }

    .red {
        color: #FF0037;
        font-size: 55px;
        font-weight: $bold-weight;
        width: 370px;
        height: 69px;
        overflow: hidden;
    }

   .span {
    display: block;
    height: 100%;
    animation: spin_words 6s infinite;
   }

   @keyframes spin_words {
    10%{
        transform: translateY(-112%);
    }
    25%{
        transform: translateY(-100%);
    }
    35%{
        transform: translateY(-212%);
    }
    50%{
        transform: translateY(-200%);
    }
    60%{
        transform: translateY(-312%);
    }
    75%{
        transform: translateY(-300%);
    }
    85%{
        transform: translateY(-412%);
    }
    100%{
        transform: translateY(-400%);
    }
   }

   &__img-mobile-block {
    display: none;
    @media (max-width: $phone-size) {
        display: block;
        position: relative;
        height: 330px
    }
   }

   .mob-img {
    @media (max-width: $phone-size) {
        display: block;
        width: 100%;
        position: absolute;
    }
   }

   .mob1 {
    @media (max-width: $phone-size) {
        max-width: 90%;
        bottom: 0;
        right: 0;
        z-index: 10000;
    }
   }

   .wave-mob1 {
    @media (max-width: $phone-size) {
        max-width: 100%;
        top: 0;
        left: 0;
    }
   }

    &__block {
        padding: 256px 0 100px 0;
        display: flex;
        justify-content: space-between;
        margin-bottom: 300px;
        @media (max-width: $phone-size) {
            padding: 0;
            margin-bottom: 90px;
        }
    }
    &__text-block{
        max-width: 438px;
    }
    &__text-title {
        line-height: 48px; 
        letter-spacing: -0.96px;
        margin-bottom: 52px;
        animation: slideInLeft 2.5s ease-out forwards;
    }

    @keyframes slideInLeft {
        0% {
            opacity: 0;
            transform: translateX(-100px);
        }
        100% {
            opacity: 1;
            transform: translateX(0);
        }
    }

    .main__text-title, .main__text-text {
        margin: 2rem 0;
        // font-size: 3rem;
        
        /* Скроем элемент в начальном состоянии */
        opacity: 0;
        transform: translateY(20px);
      }
      
      .main__text-title.element-show, .main__text-text.element-show {
        opacity: 1;
        transition: all 1.5s;
        transform: translateY(0%);
      }
    &__text-text {
        margin-bottom: 65px;
        line-height: 38.5px; 
        letter-spacing: -0.4px;
        animation: slideInLeft 2.5s ease-out forwards;
    }
    
    &__img-block {
        position: relative;
        width: 446px;
        @media (max-width: $phone-size) {
            display: none;
        }
    }
    &__img {
        max-width: 332px;
        position: absolute;
        right: 0;
        top: 23px;
    }
    &__strings {
        max-width: 446px;
        position: absolute;
        right: 0;
    }
    &__circle {
        position: absolute;
        right: -50px;
        top: -50px;
        z-index: -1;
    }

    @keyframes rotate {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(360deg);
        }
    }
    
    &__circle {
        animation: rotate 3s linear infinite;
    }

    &__img2-block {
        padding: 150px 0 400px 0;
        width: 100%;
        position: relative;
        @media (max-width: $phone-size) {
            padding: 36px 0;
        }
    }

    &__games {
        position: relative;
        margin-bottom: 200px;
    }

    &__img2-games {
        display: flex;
        justify-content: center;
        gap: 8px;
            @media (max-width: $phone-size) {
                flex-direction: column;
                align-items: center;
                gap: 60px;
        }
    }

    &__game-block {
        max-width: 300px;
        max-height: 300px;
        overflow: hidden;
        position: relative;
      }
    &__game {
        width: 100%;
        transition: transform 0.60s;
        position: relative;
        @media (max-width: $phone-size) {
            border: 3px solid $color-white;
        }
      }
      &__game-block:hover img {
        transform: scale(1.2);
        filter: brightness(0.6);
      }

      .text-overlay {
        display: none;
        @media (max-width: $phone-size) {
            display: block;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.75);
            font-size: $text2-size;
            font-weight: $bold-weight;
            width: 100%;
            text-align: center;
        }
    }
    &__game-block:hover .text-overlay {
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.75);
        font-size: $text2-size;
        font-weight: $bold-weight;
        width: 100%;
        text-align: center;
    }


}